import { observable, action, computed, when } from 'mobx';
import { getTimeUnit, getIntervalInSeconds } from './utils';
import MenuStore from './MenuStore';

export default class TimeperiodStore {
    constructor(mainStore) {
        this.mainStore = mainStore;
        when(() => this.context, this.onContextReady);
        this.menu = new MenuStore({getContext: () => this.context});
    }

    get context() { return this.mainStore.chart.context; }
    get loader() { return this.mainStore.loader; }

    @observable timeUnit = null;
    @observable interval = null;
    @observable remain = 10;

    onContextReady = () => {
        const { timeUnit, interval } = this.context.stx.layout;
        this.timeUnit = getTimeUnit({ timeUnit, interval });
        this.interval = interval;

        const stx = this.context.stx;
        var panel=stx.panels['chart'];

        // stx.addEventListener('drawing', () => {
        //     this.addRemainTimeLabel(); });
        stx.createYAxis(panel, false,true,true,panel.yaxis);
        stx.drawYAxis(panel,  [0,100] );

        const convertMilliseconds = (ms) => {
            const totalSec = ms / 1000;
            if (totalSec <= 0) { return null; }
            const padNum = (n) => (`0${n}`).slice(-2);
            const seconds = padNum(Math.trunc((totalSec) % 60));
            const minutes = padNum(Math.trunc((totalSec / 60  ) % 60));
            let hours = Math.trunc((totalSec / 3600) % 24);
            hours = hours ? `${hours}:` : '';
            return `${hours}${minutes}:${seconds}`;
        };

        setInterval(() => {
            // let diff = new Date() - stx.chart.dataSet[stx.chart.dataSet.length-1].DT;
            // const remainMilSec = (getIntervalInSeconds(stx.layout) * 1000) - diff;
            // this.remain = convertMilliseconds(remainMilSec);
            this.addRemainTimeLabel();
        }, 1000);
    }

    addRemainTimeLabel() {
        var stx = this.context.stx;
        var chart=stx.chart,
            panel=chart.panel,
            yAxis=panel.yAxis,
            xaxis=chart.xaxis,
            dataSet=chart.dataSet,
            ctx=chart.context;
        //var panel=stx.panels['chart'];
        //var yaxis = panel.yAxis;
        const hasData = (stx.chart.dataSet && stx.chart.dataSet.length) > 0;
        if(hasData){
            var price = stx.chart.dataSet[stx.chart.dataSet.length-1].Close;
            var y=stx.pixelFromPrice(price, panel);
            var x=stx.pixelFromDate(dataSet[dataSet.length-1].Date, chart);
            //stx.createYAxisLabel(panel, this.remain, y+18, "black" , "white");
            //stx.startClip(panel.name);

            //var margin={left:-100 , right:100};
            //CIQ.roundRect({ctx:ctx, x:x ,top:y+10,txt:price,radius:0,height:10, margin,width:50,fill:true, backgroundColor:"black" , color:"white"});
            //stx.endClip();
            //CIQ.createLabel({ctx:ctx, x:x ,y:y+30,txt:price, margin, backgroundColor:"black" , color:"white"});
            //ctx.beginPath();

            stx.append('draw', () => {stx.createYAxisLabel(panel, price, y+18, "black" , "white");});
            // stx.endClip();
            // var txt=12;
            // if(panel.chart.transformFunc) {txt=panel.chart.transformFunc(stx, panel.chart, txt);}
            // if(panel.yAxis.priceFormatter)
            // {txt=panel.yAxis.priceFormatter(stx, panel, txt);}
            // else
            // {txt=stx.formatYAxisPrice(txt, panel);}
            // stx.createYAxisLabel(panel, txt, y+20, "red");
            // stx.startClip(panel.name);
        }
    }

    @action.bound setPeriodicity(interval, timeUnit) {
        if (this.loader) {
            this.loader.show();
        }

        const stx = this.context.stx;
        const wasTick = stx.layout.timeUnit === 'second';
        stx.setPeriodicity({ period: 1, interval, timeUnit }, () => {
            if (this.loader) {
                this.loader.hide();
            }

            const chartType = this.mainStore.chartType;
            const isTick = timeUnit === 'second';
            const isCandle = ['candle', 'hollow_condle', 'colored_bar'].indexOf(chartType.type.id) !== -1;

            if (isCandle && isTick) {
                chartType.setType('mountain');
            } else if (!isTick && wasTick) {
                chartType.setType('candle');
            }

            this.mainStore.chart.saveLayout();
        });

        this.timeUnit = getTimeUnit(stx.layout);
        this.interval = stx.layout.interval;
        this.menu.setOpen(false);
    }

    @computed get timeUnit_display() {
        if(!this.timeUnit) {return;}
        let temp = this.timeUnit;
        if(temp.length > 4) {
            temp = (temp).slice(0,3);
        }
        return temp.replace(/(\w)/, (str) => str.toUpperCase());
    }

    @computed get interval_display() {
        if (this.interval % 60 === 0) {
            return this.interval / 60;
        }
        return +this.interval ? this.interval : 1;
    }
    @computed get display() {
        const t = this.timeUnit ? this.timeUnit[0] : '';
        return this.interval_display + t; // 1d, 1t, 5m, 2h
    }
}
